/* eslint react/no-multi-comp:0, no-console:0, react/prop-types:0 */
import 'rc-tabs/assets/index.less';
import React from 'react';
import Sortable from 'react-sortablejs';
import ReactDOM from 'react-dom';
import Tabs, { TabPane } from 'rc-tabs';
import TabContent from 'rc-tabs/lib/SwipeableTabContent';
import ScrollableInkTabBar from 'rc-tabs/lib/ScrollableInkTabBar';

const PanelContent = ({ id }) => (
  <div>{[1, 2, 3, 4].map(item => <p key={item}>{id}</p>)}</div>
);

class Demo extends React.Component {
  state = {
    activeKey: '2',
    start: 0,
    init: true,
  };

  componentDidMount() {
    // setTimeout(() => {
    //   this.setState({ init: false });
    // }, 2000);
  }

  onChange = (activeKey) => {
    console.log(`onChange ${activeKey}`);
    this.setState({
      activeKey,
    });
  }

  onTabClick = (key) => {
    console.log(`onTabClick ${key}`);
    if (key === this.state.activeKey) {
      this.setState({
        activeKey: '',
      });
    }
  }

  tick = () => {
    this.setState({
      start: this.state.start + 10,
    });
  }

  render() {
    const start = this.state.start;
    const init = this.state.init;

    const myStyle = {};
    if (init) {
      myStyle.transform = 'scale(0.4)';
    }

    return (
      <div style={{ margin: 20 }}>
        <h1>Simple Tabs</h1>
        <div style={myStyle}>
          <Tabs
            renderTabBar={() => <ScrollableInkTabBar onTabClick={this.onTabClick}/>}
            renderTabContent={() => <TabContent animatedWithMargin />}
            navWrapper={(content) => <Sortable>{content}</Sortable>}
            activeKey={this.state.activeKey}
            onChange={this.onChange}
          >
            <TabPane tab={`tab ${start}11111111111111111`} key="1">
              <PanelContent id={start} />
            </TabPane>
            <TabPane tab={`tab ${start + 1}`} key="2">
              <PanelContent id={start + 1} />
            </TabPane>
            <TabPane tab={`tab ${start + 2}`} key="3">
              <PanelContent id={start + 2} />
            </TabPane>
            <TabPane tab={`tab ${start + 3}`} key="4" disabled>
              <PanelContent id={start + 3} />
            </TabPane>
          </Tabs>
        </div>
        <button onClick={this.tick}>rerender</button>
      </div>
    );
  }
}

ReactDOM.render(<Demo />, document.getElementById('__react-content'));



